import Layout from '../../components/Layout';
import Sidebar from './sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome } from '@fortawesome/free-solid-svg-icons';
import { selectUser } from '../../redux/userReducer';
import { useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import { web3Service } from '../../services/web3.service';
import { useNavigate } from 'react-router-dom';

const Wallet = () => {
  const navigate = useNavigate();
  const seluser = useSelector(selectUser);
  const [walletDetail, setWalletDetail] = useState([]);
  useEffect(() => {
    if (seluser && seluser._id) {
      setWalletDetail([
        {
          address: "", balance: "", type: 0, name: "BTC"
        },
        {
          address: seluser.walpublic, balance: "", type: 1, name: "ETH"
        },
        {
          address: seluser.walpublic, balance: "", type: 2, name: "BNB"
        },
        {
          address: "", balance: "", type: 3, name: "XRP"
        },
        {
          address: seluser.walpublic, balance: "", type: 4, name: "USDT"
        },
        {
          address: "", balance: "", type: 5, name: "CPOS"
        }
      ]);
    } else {
      setWalletDetail([]);
    }
  }, [seluser._id])

  const getWalletAddress = (addr) => {
    return addr.substr(0, 6) + "..." + addr.substr(addr.length - 4, 4)
  }

  const getWalletBalance = async () => {    
    const ret = await web3Service.getWalletBalances(seluser._id);
    console.log(ret)
    return 0;
  }

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome} className='cursor-pointer' onClick={() => navigate('/')}/>
                <p className='ml-2 font-bold'>Wallet</p>
              </div>
              <div className='w-full flex justify-between mt-[1rem] text-gray-100 text-xl mb-2 bold grid grid-cols-3 gap-6 px-[2rem] py-[2rem]'>
                {
                  walletDetail.map((detail, index) => {
                    if (detail.type === 2 || detail.type === 3 || detail.type === 5) return null;
                    return (
                      <div key={index} onClick={() => {getWalletBalance()}} className='w-full h-[10rem] text-xl text-center rounded-[0.6rem] bg-[#D9D9D9] text-gray-800 select-none cursor-pointer px-[2rem]'>
                        <div className='text-xl text-center leading-[4rem] text-2xl select-none font-bold'>
                          {detail.name}
                        </div>
                        <div className='text-xl text-left leading-[2rem] select-none'>
                          Address: {getWalletAddress(detail.address)}
                        </div>
                        <div className='text-xl text-left leading-[2rem] select-none'>
                          Balance: 0
                        </div>
                      </div>
                    )
                  })
                }
              </div>
            </div>
            {seluser.role === 'merchant' && <Sidebar />}
        </div>
    </Layout>
  );
}

export default Wallet;